import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';

const AuthorDetail = () => {
  const { slug } = useParams();
  const [author, setAuthor] = useState(null);
  const [posts, setPosts] = useState([]); // Ensure this is initialized as an array
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchAuthorDetails = async () => {
      setLoading(true);
      setError('');
    
      try {
        // Fetch author details
        const authorResponse = await axios.get(`https://meetanshi.com/blog/wp-json/wp/v2/users?slug=${slug}`);
    
        if (authorResponse.data.length > 0) {
          const authorData = authorResponse.data[0];
          setAuthor(authorData);
    
          // Fetch posts by author
          const postsResponse = await axios.get(`https://meetanshi.com/blog/wp-json/wp/v2/posts?author=${authorData.id}&_embed`);
          
          // Log the entire posts response for debugging
          console.log('Posts Response:', postsResponse);
    
          // Check if the data is an array
          if (Array.isArray(postsResponse.data)) {
            setPosts(postsResponse.data);
          } else {
            console.error('Posts response is not an array:', postsResponse.data);
            setPosts([]); // Set to an empty array if response is not valid
          }
        } else {
          setError('Author not found.');
        }
      } catch (err) {
        console.error('Error fetching author details or posts:', err);
        setError('Failed to load author details.');
      } finally {
        setLoading(false);
      }
    };
    

    fetchAuthorDetails();
  }, [slug]);

  const getAuthorImage = () => {
    return author?.avatar_urls ? author.avatar_urls[96] : 'https://via.placeholder.com/150';
  };

  const getPostCategories = (post) => {
    if (post._embedded && post._embedded['wp:term']) {
      return post._embedded['wp:term'][0].map((category) => category.name).join(', ');
    }
    return 'Uncategorized';
  };

  const getPostImage = (post) => {
    if (post._embedded && post._embedded['wp:featuredmedia']) {
      return post._embedded['wp:featuredmedia'][0].source_url;
    }
    return 'https://via.placeholder.com/300'; // Placeholder if no image is available
  };

  if (loading) {
    return <p>Loading...</p>;
  }

  if (error) {
    return <p>{error}</p>;
  }

  if (!author) {
    return <p>No author found.</p>;
  }

  return (
    <div className="author-detail">
      <h2>{author.name}</h2>
      <img src={getAuthorImage()} alt={author.name} className="single-author-image img-fluid" />
      <div dangerouslySetInnerHTML={{ __html: author.description || 'No description available.' }}></div>
      
      <h3>Posts by {author.name}:</h3>
      {Array.isArray(posts) && posts.length > 0 ? (  // Check if posts is an array
        <div className="row">
          {posts.map((post) => (
            <div className="col-md-4" key={post.id}>
              <img 
                src={getPostImage(post)} 
                alt={post.title.rendered} 
                className="post-image img-fluid mb-2" 
              />
              <a href={`/post/${post.slug}`}><h5>{post.title.rendered}</h5></a>
              <p>{getPostCategories(post)}</p>
            </div>
          ))}
        </div>
      ) : (
        <p>No posts found by {author.name}.</p>
      )}
    </div>
  );
};

export default AuthorDetail;
